<template>
    <component :promotion="promotion" :is="view"></component>
</template>

<script>
    import StyleOne from "./shareImgStyle/styleOne";
    import styleTwo from "./shareImgStyle/styleTwo";
    import styleThree from "./shareImgStyle/styleThree";
    export default {
        name: "shareImg",
        components: {StyleOne},
        props:{
            promotion: {
                type:Object,
                default:()=>{
                    return {};
                }
            }
        },
        computed:{
            view:{
                get(){
                    let view = null;
                    if(this.promotion.hasOwnProperty('pid') ){
                        switch (this.promotion.pid) {
                            case 2:
                                view = styleTwo;
                                break;
                            case 3:
                                view = styleThree;
                                break;
                            default:
                                view = StyleOne;
                        }
                    }
                    return view;
                }
            }
        },
    }
</script>