<template>
    <div class="poster-demo" v-if="promotion">
        <div class="bg-img" :style="{backgroundColor:promotion.bgColor}">
            <diy-image :src="promotion.bgImage" v-if="promotion.isShowBg != 0" fit="fill"></diy-image>
        </div>
        <div class="poster-msg">
            <div class="info-tit">
                    <p class="info-txt" v-if="promotion.isTitle != 0"
                       :style="{color: promotion.extTColor,fontSize: promotion.extTSize,fontFamily:promotion.extTFont}"
                       style="font-weight:700;">{{ promotion.extTitle }}</p>
                    <p class="info-txt" v-if="promotion.isSubTitle != 0"
                       :style="{color:promotion.extSubTColor,fontSize: promotion.extSubTSize,fontFamily:promotion.extSubTFont}">{{ promotion.extSubTitle }}</p>
            </div>
            <div class="info-user">
                <div class="info-user-box">
                    <diy-image fit="contain" v-if="promotion.headType != 0" :src="promotion.headType != 2 ? promotion.userHead : promotion.shopHead" class="user-img"></diy-image>
                    <p class="info-txt" :class="{margin:promotion.headType != 0}">
                        {{ promotion.username }}
                    </p>
                    <p class="info-txt" :class="{margin:promotion.headType != 0}">
                        {{ promotion.shopName }}
                    </p>
                </div>
            </div>
            <div class="info-tips-1" v-if="promotion.isTip1 != 0"
                 :style="{color: promotion.extTColor1,fontSize: promotion.extTSize1,fontFamily:promotion.extTFont1}">{{ promotion.extTip1 }}</div>
            <div class="info-tips-2" v-if="promotion.isTip2 != 0"
                 :style="{color: promotion.extTColor2,fontSize: promotion.extTSize2,fontFamily:promotion.extTFont2}">{{ promotion.extTip2 }}</div>
            <div class="info-code">
                <diy-image fit="contain" :src="promotion.qrCode" class="img"></diy-image>
            </div>
        </div>
    </div>
</template>

<script>
    import DiyImage from "../../common/diyImage";
    export default {
        name: "styleOne",
        components: {DiyImage},
        props: {
            promotion: {
                type: Object,
                default: () => {
                    return {};
                }
            }
        },
    }
</script>

<style lang="scss" scoped>
    .poster-demo {
        position: relative;
        width: 100%;
        height: 500px;
        .bg-img {
            position: absolute;
            width: 100%;
            height: 100%;
            right: 0;
            top: 0;
            overflow: hidden;
            z-index: 0;
            /deep/ .el-image__inner{
                width: inherit;
                height: inherit;
            }
        }

        .poster-msg {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 100;

            .info-tit{
                position: relative;
                text-align: center;
                color: #fff;
                margin-top: 30px;

                .info-txt {
                    font-size: 28px;
                    white-space:nowrap;/*不显示的地方用省略号...代替*/
                    text-overflow:ellipsis;/* 支持 IE */
                    overflow: hidden;
                }
            }
            .info-user {
                position: absolute;
                text-align: center;
                color: #fff;
                top: 150px;
                width: 100%;
                left: 0;
                z-index: 290;

                .info-user-box{
                    position: relative;
                    display: inline-block;
                    .info-txt{
                        font-size: 14px;

                        &.margin{
                            margin-left: 60px;
                        }
                    }
                    .user-img{
                        position: absolute;
                        width: 45px;
                        top: 50%;
                        left: 0;
                        transform: translateY(-50%);
                    }
                }
            }

            .info-tips-1 {
                position: absolute;
                z-index: 190;
                bottom: 180px;
                left: 50%;
                text-align: center;
                transform: translateX(-50%);
                white-space:nowrap;/*不显示的地方用省略号...代替*/
                text-overflow:ellipsis;/* 支持 IE */
                overflow: hidden;
                width: 80%;
                font-size: 12px;
            }

            .info-tips-2 {
                position: absolute;
                z-index: 190;
                bottom: 120px;
                left: 50%;
                text-align: center;
                transform: translateX(-50%);
                /*text-overflow: ellipsis;*/
                /*display: -webkit-box;*/
                /*-webkit-line-clamp: 2;*/
                /*-webkit-box-orient: vertical;*/
                max-height: 48px;
                overflow: hidden;
                width: 130px;
                font-size: 20px;
                line-height: 1.2;
            }

            .info-code {
                width: 70px;
                height: 70px;
                position: absolute;
                bottom: 30px;
                left: 50%;
                transform: translateX(-50%);
                z-index: 200;
                /deep/ .el-image__inner{
                    width: inherit;
                    height: inherit;
                }
            }
        }
    }
</style>