<template>
    <div class="download">
        <diy-image :src="data.img"></diy-image>
    </div>
</template>

<script>
    import DiyImage from "./diyImage";
    export default {
        name: "downloadImg",
        components: {DiyImage},
        props:{
            data:{
                type:Object,
                default:()=>{
                    return {};
                }
            }
        },
        created(){
            if(this.data.hasOwnProperty('img')){
                let a = document.createElement('a');
                // 将a的download属性设置为我们想要下载的图片名称
                a.download = 'share';
                // 将生成的URL设置为a.href属性
                a.href = this.data.img;
                // 触发a的单击事件
                a.click();
            }
        }
    }
</script>

<style lang="scss" scoped>
    .download{
        padding: 20px;
    }
</style>